* {
     transform: all();
     transition-duration: 200ms;
}


html {
     scroll-behavior: smooth;

     p,
     h6,
     h5,
     h4,
     h3 {
          -webkit-hyphens: auto;
          -ms-hyphens: auto;
          hyphens: auto;
     }
}

#master-container {
     width: 100vw;
     /* max-width:1848px; */
     position: absolute;
     top: 80px;
     left: 0;
     right: 0;
     margin: auto;
     background-color: var(--secondColor);
     min-height: calc(100vh - 80px);
     
     @include breakpoint('s') {
          overflow-x: hidden;
          top: 16vw;
     }

     #content-container {
          min-height: calc(100vh - 280px);
         
     }
}

.video-header {
     top: 0 !important;
}

.se-loader {
     min-height: 40vh;
     width: 100%;
}


