.session-grid-container {
     .session-slot-container {

          .session-slot-title {
               margin: 20px 0;
          }

          .session-slot-facts {
               margin: 5px 0;

          }

          .session-blocks-container {
               display: flex;
               flex-wrap: wrap;


          }
     }
}

//BLOCK 
.session-block {

     width: 47%;
     margin: 40px 1.5%;
     cursor: pointer;

     @include breakpoint('m') {
          width: 100%;
          margin: 40px 0;
     }

     &:hover {
          @include dropShadow;

          .session-block-content {
               padding: 25px 20px;
          }

          .session-block-header-image div {
               transform: scale(1.1);
          }
     }

     .session-block-header {
          position: relative;
          width: 100%;
          height: 160px;

          .session-block-header-image {
               height: 100%;
               width: 100%;
               @include imageSettings;
               overflow: hidden;

               div {
                    height: 100%;
                    width: 100%;
                    @include imageSettings;
               }
          }

          .session-block-sponsors {
               position: absolute;
               bottom: -15px;
               right: 15px;
               display: flex;
               justify-content: space-around;
               align-items: center;
               flex-wrap: wrap;
               background-color: $light;
               padding: 10px;
               text-align: center;
               @include dropShadow;

               H6 {
                    width: 100%;
                    opacity: .5;
               }

               .session-block-sponsor {

                    display: flex;
                    justify-content: center;
                    align-items: center;

                    height: 80px;
                    width: 80px;

                    img {
                         max-width: 90%;
                         max-height: 80%;
                    }

               }
          }

     }

     .session-block-content {
          padding: 30px 0;

          p {
               line-height: 1.6em;
               margin: 10px 0;
          }

     }
}



//LIGHTBOX
.session-lb-container {
     position: fixed;
     top: 0;
     left: 0;
     width: 87vw;
     height: 100vh;
     background-color: var(--secondColor);

     .session-lb-head {

          .session-lb-image {
               width: 100%;
               height: 20vh;
               min-width: 220px;
               min-height: 250px;
               @include imageSettings;
          }

          .session-lb-content {
               height: 70vh;
               padding: 5% 15%;
               width: 100%;
               overflow-y: scroll;
               @include scrollbars(6px, var(--lightshade));
               .session-lb-language {
                    background-color: var(--shadeColor);
                    border-radius: 15px;
                    padding: 2px 10px;
                    font-size: .8rem;
                    margin: 2px;
                    width: fit-content;
               }


               .session-lb-sponsors {
                    margin-top: 20px;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    justify-content: center;

                    h6:first-of-type {
                         width: 100%;
                         text-align: center;
                    }

                    .session-lb-sponsor {
                         width: 20%;
                         margin: 20px 20px;
                         min-width: 80px;

                         img {
                              width: 100%;
                         }
                    }
               }
          }




     }
}